.mainContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 20px 40px;
}

.footer-box{
  position: relative;
  width: 100%;
}
.footer{
  background: var(--primary-color);
  color: white;
  text-align: center;
  font-weight: lighter;
  padding: 10px;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



@media only screen and (max-width: 600px) {
  .mainContainer {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}